.linechart {
  padding: 8px;
}
.linechart_path {
  stroke-width: 3;
  fill: none;
}

.linechart_axis {
  stroke: #bdc3c7;
}

.linechart_point {
  fill: #fff;
  stroke-width: 2;
}

.linechart_area {
  padding: 8px;
  fill: #ccf70d;
  stroke: none;
  opacity: .4;
}

.linechart_label {
 fill: #ccf70d;
 font-weight: 700;
}

.hoverLine {
  stroke: #7D95B6;
  stroke-width: 1;
}

.hover {
  display: flex;
  flex-direction: column;
  align-items: center;

  position: absolute;
  padding: 5px;
  border-radius: 4px;
  background-color: #ccf70d;
  color: black;
}

.hover .price {
  font-weight: 700;
}

